.probstmt {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 30px;
  margin: auto;
}

.circle {
  float: left;
  width: 350px;
  height: 350px;
  shape-outside: circle();
  background-color: #4a4947;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.probstmt .circle-p {
  width: 1000px;
  height: 250px;
  background-color: #b17457;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: justify;
  font-size: 18px;
  color: #faf7f0;
}

.circle p {
  font-size: 50px;
  font-weight: 700;
  color: #faf7f0;
  text-align: center;
  font-style: italic;
}
